.header {
  background: url("../images/header-bg.jpg") no-repeat;
  background-position: right;
  background-size: cover;

  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__bottom {
    padding-bottom: 88px;

    @include onTablet {
      padding-bottom: 130px;
    }
  }

  &__title {
    margin: 0;
    font-size: 36px;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -1.5px;
    text-align: center;

    @include onTablet {
      font-size: 64px;
    }
  }
}
